import styled from '@emotion/styled';
import React from 'react';
const CalendarHeatmap = React.lazy(() => import('react-calendar-heatmap'));
const PieChart = React.lazy(() => import('./PieChart'));
import 'react-calendar-heatmap/dist/styles.css';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import ReactTooltip from 'react-tooltip';
import siteInfo from '../../config/site-info';
import media from '../../utils/media';

const AboutDiv = styled.div`
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
    background-color: #fff;
    .resume {
        text-align: center;
        font-size: 24px;
    }
    .about-header {
        flex: 1 0 auto;
        display: flex;
        flex-direction: row;
        border-bottom: 2px solid #444;
        .person-base {
            flex: 1 0 auto;
            .name {
                font-size: 20px;
            }
            .aim {
                font-size: 12px;
            }
            ${media.phone`
                display: none;
            `};
        }
        .person-info {
            flex: 1 0 auto;
            padding: 5px;
            list-style: none;
            li {
                font-size: 12px;
                padding: 5px;
            }
        }
        .person-img {
            flex: 1 0 auto;
            text-align: right;
            img {
                width: 90px;
                height: 90px;
                border-radius: 2px;
            }
            h4{
                display: none;
            }
            ${media.phone`
                flex: auto;
                text-align: left;
                img{
                    width: 70px;
                    height: 70px;
                }
                h4{
                    font-size: 12px;
                    display: block;
                }
            `};
        }
    }
    .about-main {
        display: flex;
        flex-direction: column;
        ${media.phone`
            padding-left: 8px;
        `};
    }
    .about-main-item {
        flex: 1 0 auto;
        .about-main-title{
            flex: 0 0 auto;
            i {
                vertical-align: text-bottom;
                margin-left: 5px;
            }
        }
        .about-main-content{
            font-size: 14px;
            line-height: 26px;
            padding: 10px;
            margin: 0;
            h4{
                margin:  0;
            }
            p{
                margin: 5px 0;
            }
        }
    }
`;

const today = new Date();

class About extends React.Component<any, any> {
    public render() {
        const userProfile = this.props._DB.profile;
        let values = [];
        const userCommits = userProfile.userCommits;
        const userRepos = userProfile.userRepos;
        const totalContributionLastYear = userProfile.userCommits && userProfile.userCommits.total || 0;
        if (userProfile && userProfile.userCommits && userProfile.userCommits.contribution) {
            values = userProfile.userCommits.contribution.map((item: any) => {
                return {
                    year: item.year,
                    date: item.year + '-' + item.month,
                    count: item.count,
                };
            });
        }
        return (
            <AboutDiv className="about">
                <Helmet title={siteInfo.name + '-'}></Helmet>
                <h1 className="resume">----</h1>
                <p style={{ borderLeft: '3px solid #ccc', padding: '5px 10px', backgroundColor: '#efefef' }}>
                    web coding
                </p>
                <div className="about-header">
                    <div className="person-base">
                        <h2 className="name"></h2>
                        <h4 className="aim">web</h4>
                    </div>
                    <ul className="person-info">
                        <li><i className="fa fa-user fa-fw"></i>2*</li>
                        <li><i className="fa fa-phone fa-fw"></i>185(*^_^*)7248</li>
                        <li><i className="fa fa-flag fa-fw"></i></li>
                    </ul>
                    <ul className="person-info">
                        <li><i className="fa fa-map-marker fa-fw"></i></li>
                        <li><i className="fa fa-envelope fa-fw"></i>bs32g1038@163.com</li>
                        <li><i className="fa fa-university fa-fw"></i></li>
                    </ul>
                    <div className="person-img">
                        <img src="/public/images/avatar.png" alt="" />
                        <h4 className="aim"><br />web</h4>
                    </div>
                </div>
                <p>
                    {totalContributionLastYear} contributions in the last year
                </p>
                <CalendarHeatmap
                    startDate={(values[0] && values[0].year + '-1-1') || (new Date().getFullYear()) - 1 + '-1-1'}
                    endDate={today}
                    values={values}
                    classForValue={(value: any) => {
                        if (!value) {
                            return 'color-empty';
                        }
                        return `color-github-${value.count}`;
                    }}
                    tooltipDataAttrs={(value: any) => {
                        if (!value.date) {
                            return {};
                        }
                        return {
                            'data-tip': `${value.date} has count: ${value.count}`,
                        };
                    }}
                    showWeekdayLabels={true}
                />
                <ReactTooltip></ReactTooltip>
                <div style={{ display: 'flex' }}>
                    <h3 style={{ minWidth: 140 }}>Stars per Repo(top 12)</h3>
                    <PieChart userRepos={userRepos}></PieChart>
                </div>
                <div className="about-main">
                    <div className="about-main-item">
                        <h4 className="about-main-title"><i className="fa fa-leaf"></i></h4>
                        <div className="about-main-content">
                            <li> photoshop  axurepr </li>
                            <li>w3c div+css ;  javascript  dom 
                            </li>
                            <li>JS() bootstrapvue react jQuery</li>
                            <li> html5  css3 </li>
                            <li>gitnodejsnpm, docker</li>
                        </div>
                    </div>
                    <div className="about-main-item">
                        <h4 className="about-main-title"><i className="fa fa-graduation-cap fa-fw"></i></h4>
                        <ul className="about-main-content">
                            <li>
                                <h4></h4>
                                <p>ui</p>
                                <ul className="person-info">
                                    <li>react,nodejsweb</li>
                                    <li>webpack</li>
                                    <li>nodejs express mongodbmongoose</li>
                                    <li>: https://github.com/bs32g1038/node-blog</li>
                                </ul>
                            </li>
                            <li>
                                <h4></h4>
                                <p></p>
                                <p>antdesignreactwebpack

</p>
                            </li>
                            <li>
                                <h4></h4>
                                <p></p>
                                <p>bootstrapjqhtmlcssjsimagesgulp</p>
                            </li>
                            <li>
                                <h4>WEB</h4>
                                <p>- </p>
                                <p>javaweb
reactnodejsantdesign
</p>
                            </li>
                            <li>
                                <h4>webapp</h4>
                                <p>webapp</p>
                                <p>vue</p>
                            </li>
                        </ul>
                    </div>
                    <div className="about-main-item">
                        <h4 className="about-main-title"><i className="fa fa-edit fa-fw"></i></h4>
                        <div className="about-main-content">
                            <li></li>
                            <li></li>
                            <li></li>
                        </div>
                    </div>
                </div>
            </AboutDiv>
        );
    }
}

export default connect(
    (state: any) => ({
        _DB: state.about
    })
)(About as any);